<template>
    <flipbook :class="`${styles ? styles : ''} flipbook`" :zooms="[1,1.5,2]" :pages="pageParsed" ></flipbook>
</template>

<script>
import Flipbook from 'flipbook-vue/vue2'
export default {
  props: {
    url: String,
    amount: String,
    styles: String,
  },
  components: {
    Flipbook
  },
  computed: {
    pageParsed() {
      let pages = []
      for(let i=1; i<Number(this.amount)+1; i++) {
        pages.push(`${this.url}${i}.jpg`)
      }
      return pages
    }
  }

}
</script>

<style>

.flipbook {
  height: 90vh;
}
.viewport {
  -webkit-overflow-scrolling: touch;
  width: 100%;
  height: 100%;
}

.viewport.zoom {
  overflow: scroll;
}

.viewport.zoom.drag-to-scroll {
  overflow: hidden;
}

.flipbook-container {
  position: relative;
  width: 100%;
  height: 100%;
  transform-origin: top left;
  user-select: none;
}

.click-to-flip {
  position: absolute;
  width: 50%;
  height: 100%;
  top: 0;
  user-select: none;
}

.click-to-flip.left {
  left: 0;
}

.click-to-flip.right {
  right: 0;
}

.bounding-box {
  position: absolute;
  user-select: none;
}

.page {
  position: absolute;
  backface-visibility: hidden;
}

.polygon {
  position: absolute;
  top: 0;
